header{
    width: 100%;
    height: 60px;
    position: fixed;
    background: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(30px);
    border : 1px solid #EBEBEB;
    z-index: 2;
}

.navbar{
    max-width: 1172px;
    height: 100%;
    margin: auto;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .logo{
        padding-top: 5px;
    }
    
    ul{
        display: flex;
        justify-content: space-between;

    }

    li{
        padding-right: 44px;
        &:last-child{
            padding-right: 0px;
        }

        @media (max-width: 425px){
            padding-right: 16px;
        }

        a{
            padding: 8px 12px 8px 12px;
        }

        a:hover{
            background-color: rgba(98, 115, 140, 0.1);
            border-radius: 8px;
        }
    }

    @media (max-width: 1024px) {
        padding: 0px 16px 0 16px;
    }
}

.nav-item{
    @media (max-width:530px) {
        &:first-child{
            display: none;
        }
    }
}