
.blog-content{
    padding: 100px 0 80px 0;

    @media (max-width:425px) {
        padding: 80px 0 48px 0;
    }
}

.start{
    display: flex;
    flex-direction: column;

    h2{
        padding-top: 32px;
    }
}

.blog-img{
    margin-top: 32px;
    img{
        max-width: 800px;
        width: 100%;
    }
}

.qk-info{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 32px;

    @media (max-width: 425px) {
        flex-direction: column;
    }
}

.qk-colomn{
    margin-right: 32px;
    &:last-child{
        margin-right: 0px;
    }
    h5{
        padding: 8px 0 0 0;
    }
    p{
        padding: 0 0 8px 0;
    }

    @media (max-width: 425px) {
        margin-right: 0;
        padding-bottom: 32px;

        &:last-child{
            padding-bottom: 0;
        }
    }
}

.qk-ul{
    margin-left: 24px;
    list-style-type: disc;
}

.number-ul{
    margin-left: 24px;
    list-style-type:decimal;
}

.note{
    background-color: #E6EFFB;
    padding: 16px 24px 16px 24px;
    border-radius: 0px 8px 8px 0px;
    margin: 24px 0 24px 0;

    .note__span{
        display: flex;
        flex-direction: row;
    }

    .note__text{
        display: flex;
        flex-direction: column;
    }

    .note__tag{
        padding-top: 16px;
        font-style: normal;
        opacity: 0.6;
    }

    hr{
        border-left: 3px solid #065BDA;
        margin: -16px 24px -16px -24px;
    }
    
    span{
        font-style: italic;
    }
}

.null{
    list-style-type: none;
    margin-left: 0px;
    padding-left: 0px !important;
}

.second-section{
    background-color: #F8F9FA;
}

.doted-ul{
    margin-left: 24px;
    list-style-type: disc;
}

.showcase{
    padding: 0 24px 96px 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (max-width:640px) {
        flex-direction: column;
        padding: 0 0 96px 0px;
    }

    @media (max-width: 425px){
        padding-bottom: 64px;
    }

    &:last-child{
        padding-bottom: 0;
    }

    .showcase__text{
        max-width: 375px;

        @media (max-width:640px) {
            text-align: center;
        }
    }

    .showcase__img{
        img{
            padding-right: 32px;
            max-width: 306px;
            width: 100%;

            @media (max-width:640px) {
                padding-right: 0;
                padding-bottom: 24px;
            }
        }
    }
}

.projects{
    .projects__right-left{
        margin-right: 40px;

        @media (max-width: 700px){
            padding-bottom: 56px;

            &:last-child{
                padding-bottom: 0;
            }
        }

        h4{
            max-width: 375px;

            @media (max-width:700px) {
                max-width: none;
            }
        }

        &:last-child{
            margin-right: 0;
        }

        @media (max-width:700px) {
            margin-right: 0;
        }

    }
    .projects__heading{
        display: flex;
        justify-content: space-between;
    }

    .projects__sources{
        display: flex;
        justify-content: space-between;

        @media (max-width:700px) {
            flex-direction: column;
            align-items: center;
        }
    }

    .projects__img{
        img{
            margin-bottom: 24px;
            max-width: 380px;
            width: 100%;

            @media (max-width:700px) {
                max-width: 700px;
            }
        }
    }
}

.time-to-read{
    display: flex;
    padding: 16px 0 16px 0;
    img{
        max-width: 32px !important;
        padding-right: 4px;
    }
}

.images-row{
    display: flex;
    justify-content: space-between;

    @media (max-width:450px) {
        flex-direction: column;
    }
}