@import './Global.scss';

*{
    color: rgba(0, 0, 0, 0.87);
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}



html, body{
    font-size: 18px;
    line-height: 25px;
    letter-spacing: 0.1px;
    text-align: left;
    font-weight: 400;
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
}

html {
    overflow-x: hidden;
}


ul{
    list-style: none;
}

a{
    text-decoration: none;
    color: rgba(0, 0, 0, 0.7);
    letter-spacing: 0.2 px;
    font-size: 17px;
    font-weight: 400;
    line-height: 20px;
    &:hover{
        color: rgba(0, 0, 0, 0.87);
    }
}

.hr-grey{
    opacity: 0.3;
}

.main-btn{
    background-color: #065BDA;
    color: white;
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 0.4;
    text-align: center;
    line-height: 24px;
    padding: 16px 32px 16px 32px;
    border-radius: 10px;
    box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.08);
    margin-right: 32px;
    &:hover{
        background-color: #0a4eb4;
        color: white;
    }
    @media (max-width: 1015px) {
        margin-bottom: 20px;
        margin-right: 0px;
    }
}

.disable-btn{
    background-color: #E5E5E5;
    color: #989898;
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 0.4;
    text-align: center;
    line-height: 24px;
    padding: 16px 32px 16px 32px;
    border-radius: 10px;
    margin-right: 32px;

    @media (max-width: 1015px) {
        margin-bottom: 20px;
        margin-right: 0px;
    }
}

.second-btn{
    background-color: none;
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 0.4;
    text-align: center;
    line-height: 24px;
    @media (max-width: 1015px){
        padding-left: 16px;
    }
    

    &::after{
        content: url(./assets/arrow-right.svg);
        position: relative;
        top: 6px;
        padding-left: 8px;
        width: 24px;
        height: 24px;
    }

    &:hover::after{
        content: url(./assets/arrow-right-active.svg);
    }
}

.dribble-btn{
    background-color: none;
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 0.4;
    text-align: center;
    line-height: 24px;

    &::after{
        content: url(./assets/Dribble.svg);
        position: relative;
        top: 6px;
        padding-left: 8px;
        width: 24px;
        height: 24px;
    }

    &:hover{
        color: #D95888;
    }

    &:hover::after{
        content: url(./assets/Dribbble-hover.svg);
    }

    @media (max-width: 425px) {
        font-size: 0px;
    }
}


h1{
    font-size: 56px;
    line-height: 68px;
    letter-spacing: 0.2px;
    font-weight: 700;
    margin-bottom: 16px;

    @media (max-width: 1024px) {
        font-size: 48px;
        line-height: 58px;
    }

    @media (max-width: 768px){
        font-size: 36px;
        line-height: 46px;
    }
}

h2{
    font-size: 42px;
    line-height: 50px;
    letter-spacing: 0.1px;
    font-weight: 700;
    margin-bottom: 16px;

    @media (max-width: 1024px) {
        font-size: 32px;
        line-height: 42px;
    }

    @media (max-width: 768px){
        font-size: 30px;
        line-height: 40px;
    }
}

h3{
    font-size: 36px;
    line-height: 43px;
    letter-spacing: 0.1px;
    font-weight: 700;
    margin-bottom: 16px;

    @media (max-width: 768px){
        font-size: 28px;
    }
}

h4{
    font-size: 26px;
    line-height: 31px;
    letter-spacing: 0.1px;
    font-weight: 700;
    margin-bottom: 16px;

    @media (max-width: 768px){
        font-size: 24px;
    }
}

h5{
    font-size: 22px;
    line-height: 26px;
    letter-spacing: 0.1px;
    font-weight: 700;
    margin-bottom: 8px;
}

h6{
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0.8px;
    font-weight: 600;
    margin-bottom: 8px;
    color: rgba(0, 0, 0, 0.6);

    @media (max-width: 425px){
        font-size: 18px;
    }

}

.p-heading{
    font-size: 20px;
    color: $secondColor;
}

.link-in-text{
    color: #065BDA;
    &:hover{
