.hero{
    padding-top: 120px;
    //background-image: url(../assets/home-background.png)
    background: linear-gradient(180deg, #E2EDFF 0%, rgba(255, 255, 255, 0) 62.58%);
}

.content-heading{
    .heading{
        text-align: center;
        max-width: 528px;
    }
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.hero-image{
    overflow-x: hidden;
    img{
        max-width: 1176px;
        width: 100%;
        padding-top: 56px;
        
        @media (max-width: 768px) {
            width: 180%;
            margin-left: -48px;
        }

        @media (max-width: 480px) {
            width: 250%;
            margin-left: -32px;
            padding-top: 32px;
        }
    }
}


