@import '../Global.scss';

.footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: #F8F9FA;
    border : 1px solid #EBEBEB;
    
    h3{
        padding-top: 40px;
    }

    p{
        color: $secondColor;
        
    }
}

.footer-text{
    max-width: 508px;
    padding: 0 16px 0 16px;
}

.footer-icons{
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 0 16px 0 16px;
}

.icon{
    margin: 32px 0 40px 0;
    padding-right: 56px;
    &:last-child{
        padding-right:0px;
    }
}

.email-icon{

    &:hover{
        content: url(../assets/Email-hover.svg);
    }

}

.linkedin-icon{

    &:hover{
        content: url(../assets/LinkedIn-hover.svg);
    }
    
}

.dribble-icon{

    &:hover{
        content: url(../assets/Dribbble-hover.svg);
    }
    
}