.cases{
    margin-top: 100px;

    @media(max-width:425px){
        margin-top: 40px;
    }
}

.container-cases{
    max-width: 1176px;
    width: 100%;
    margin: 0 auto;

}

.content-cases{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 160px;
    &:last-child{
        margin-bottom: 200px;

        @media (max-width: 425px){
            margin-bottom: 140px;
        }
        @media (max-width: 375px){
            margin-bottom: 100px;
        }
    }

    @media (max-width: 934px) {
        flex-direction: column;
        text-align: center;
    }

    @media (max-width: 768px){
        flex-direction: column;
        margin-bottom: 100px;
    }
}

.line-cases{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 0 100px 0;

    hr{
       //transform: rotate(90deg);
       width: 100%;
       max-height: 1px;
       opacity: 0.2;
    }

    .line-text{
        font-size: 20px;
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;        
        letter-spacing: 0.4px;
        color: rgba(0, 0, 0, 0.3);;
        padding: 0 8px 0 8px;
        min-width: 220px;
        text-align: center;

        @media (max-width: 425px) {
            min-width: 190px;
        }
        @media (max-width: 375px) {
            min-width: 160px;
            font-size: 18px;
        }
    }
}

.left{
    @media (max-width: 934px) {
        margin-bottom: 40px;
    }
    @media (max-width: 768px){
        text-align: center;
    }
}

.left-text{
    h2{
        padding-bottom: 24px;

        @media (max-width: 768px){
            padding-bottom: 16px;
        }
    }
    h6{
        padding-bottom: 16px;
        @media (max-width: 768px){
            padding-bottom: 8px;
        }
    }
    
    @media (max-width: 934px) {
        max-width: 600px;
    }

    @media (max-width: 768px){
        text-align: center;
    }
}

.left-buttons{
    @media (max-width: 1015px) {
        display: flex;
        flex-direction: column;
        max-width: 500px;
        margin: 0 auto;
    }
}

.right{
    position: relative;
    img{
        max-width: 690px;
        max-height: 518px;
        margin-left: 72px;
        @media (max-width: 1125px) {
            max-width: 580px;
        }
        @media (max-width: 1024px) {
            max-width: 580px;
        }
        @media (max-width: 1000px) {
            margin-left: 48px;
        }
        @media (max-width: 934px){
            margin-left: 0;
        }

        @media (max-width: 768px){
            margin: 0 !important;
            width: 100%;
        }
    }
}

